<template>
  <el-container class="container-home">
    <el-header>
      <div class="head-left">
        <div class="block"><el-avatar shape="circle" :size="55" :src="circleUrl"></el-avatar></div>
        <!-- <el-avatar src="../assets/img1.jpeg"></el-avatar> -->
        <!-- <img src="../assets/img1.jpeg" alt="" /> -->
        <span>电商后台管理系统</span>
      </div>
      <el-button type="info" round @click="loginOut">退出</el-button>
    </el-header>
    <el-container>
      <el-aside :width="toCollapse ? '64px ' : '200px'">
        <el-menu :default-active="activePath" :collapse="toCollapse" router :collapse-transition="false" unique-opened:collapse="toCollapse" class="el-menu-vertical-demo" background-color="#343c4b" text-color="#fff" active-text-color="#409eff">
          <div class="menu-fold" @click="clickFold">
            <i class="el-icon-s-operation"></i>
          </div>
          <!-- 一级菜单 -->
          <el-submenu :index="item.id + ''" v-for="item in mentlist" :key="item.id">
            <!-- 一级菜单模板区域 -->
            <template slot="title">
              <!-- 图标 -->
              <i :class="iconObj[item.id]"></i>
              <!-- 文本 -->
              <span>{{ item.authName }}</span>
            </template>

            <!-- 二级菜单 -->
            <el-menu-item @click="clickMenu('/' + subItem.path)" :index="'/' + subItem.path" v-for="subItem in item.children" :key="subItem.id">
              <!-- 二级菜单模板 -->
              <template slot="title">
                <!-- 图标 -->
                <i class="el-icon-menu"></i>
                <!-- 文本 -->
                <span>{{ subItem.authName }}</span>
              </template>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <!-- main区域 -->
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  name: 'Home',
  data() {
    return {
      circleUrl: 'https://xiao-123.pek3b.qingstor.com/img1.jpeg',
      mentlist: [],
      iconObj: {
        125: 'iconfont icon-user',
        103: 'iconfont icon-tijikongjian',
        101: 'iconfont icon-shangpin',
        102: 'iconfont icon-shangpin',
        145: 'iconfont icon-baobiao'
      },
      // 是否折叠
      toCollapse: true,
      // 动态绑定点击二级菜单高亮
      activePath: ''
    }
  },
  created() {
    this.getMenuList()
    this.activePath = window.sessionStorage.getItem('activePath')
  },
  methods: {
    // 退出登录
    loginOut() {
      window.sessionStorage.clear('token')
      this.$router.push('./login')
    },
    async getMenuList() {
      const { data: res } = await this.$http.get('/menus')
      this.mentlist = res.data
    },
    // 是否折叠
    clickFold() {
      this.toCollapse = !this.toCollapse
    },
    // 点击二级菜单高亮
    clickMenu(urlPath) {
      window.sessionStorage.setItem('activePath', urlPath)
      this.activePath = urlPath
    }
  }
}
</script>

