<template>
  <div class="login_container">
    <div class="login_box">
      <!-- logo区域 -->
      <div class="logo_icon">
        <img src="../assets/logo.png" alt="" />
      </div>

      <!-- from表单区域 -->
      <el-form :model="login_from" :rules="loginRules" ref="loginFormRef" label-width="0px" class="logo_form">
        <!-- 用户名 -->
        <el-form-item prop="username">
          <el-input v-model="login_from.username" prefix-icon="el-icon-user"></el-input>
        </el-form-item>

        <!-- 密码框 -->
        <el-form-item prop="password">
          <el-input type="password" v-model="login_from.password" prefix-icon="el-icon-goods"></el-input>
        </el-form-item>

        <el-form-item class="login_btn">
          <el-button type="primary" @click="loginForm">登录</el-button>
          <el-button type="info" @click="resetForm">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data() {
    return {
      login_from: {
        // 登录表单用户名和密码
        username: 'admin',
        password: '123456'
      },
      loginRules: {
        // 表单验证用户名
        username: [
          { required: true, message: '请输入用户名称', trigger: 'blur' },
          { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
        ],
        // 表单验证用户密码
        password: [
          { required: true, message: '请输入用户密码', trigger: 'blur' },
          { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 重置表单
    resetForm() {
      // console.log(this)
      this.$refs.loginFormRef.resetFields()
    },
    // 表单登录
    loginForm() {
      this.$refs.loginFormRef.validate(async val => {
        if (!val) return
        const { data: res } = await this.$http.post('/login', this.login_from)
        if (res.meta.status !== 200) return this.$message.error('登录失败了哦！')
        this.$message.success('登录成功！')
        // 登录成功后跳转/home组件
        console.log(res)
        // 保存登录成功后的token到session存储
        window.sessionStorage.setItem('token', res.data.token)
        // 编程导航跳转到Home组件
        this.$router.push('/home')
      })
    }
  }
}
</script>

